<template>
  <div id="tag-wrapper">
	  <RoomTag v-for="item in items" :data="item" />
  </div>
</template>


<script type="text/javascript">
import RoomTag from './RoomTag.vue'

export default {
  name: 'tag-list',
  components: { RoomTag, },
  data: function() {
    return {
      items: ['양파', '여자만', '돼지고기']
    }
  }
}

</script>


<style scoped>
#tag-wrapper {
  border: 0px;
  margin-top: 25px;
  width: 330px;
  height: 40px;
  display: flex;
  flex-wrap: wrap;
  /*flex-direction: row;*/
  /*align-items: center;*/
  justify-content: center;
  margin-left: 5px;
}
</style>


