  <p id="room-name">{{ roomName }}</p>
  <img src="../assets/ic-map@3x.png" id="pin-icon" />
  <p id="location">장소 : {{ location }}</p>
  <img src="../assets/ic-time@3x.png" id="time-icon" />
  <p id="time">시간 : {{ time }}</p>
  <RoomTagList></RoomTagList>
</div>
</template>


<script type="text/javascript">
import RoomTagList from './RoomTagList'

export default {
  name: 'room',
  data: function() {
    return {
      userName: '당당한 양파',
      roomName: '오늘 우리 효신이 실검 1위했당 정말 최고...',
      location: '영등포구청 3번출구',
      time: '저녁 7시',
      tags: ['양파', '여자만', '돼지고기']
    }
  },
  components: { RoomTagList, }
}
</script>


<style scoped>
@import url(https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css);

#wrapper {
  width: 355px;
  height: 212px;
  border-radius: 4px;
  box-shadow: -0.3px 0.9px 6px 0 rgba(0, 0, 0, 0.07);
  background-color: #ffffff;
  border: solid 1px #dfdfdf;
  margin-left: 0px;
  margin-right: 21px;
  margin-bottom: 20px;
}

#profile-image {
  width: 40px;
  height: 40px;
  margin-top: 18px;
  float: left;
  margin-left: 24px;
  margin-right: 8px;
}

#user-name {
  font-family: 'NanumSquare';
  font-size: 16px;
  color: #1d1d1d;
  text-align: left;
  margin-top: 28px;
  /*margin-left: 8px;*/
}

#room-name {
  font-family: 'NanumSquare';
  font-size: 18px;
  color: #1d1d1d;
  /*text-align: left;*/
  margin-top: 23px;
  margin-left: 7px;
}

#location {
  float: left;
  font-family: 'NanumSquare';
  font-size: 16px;
  color: #666666;
  margin-top: 10px;
  margin-left: 5px;
}

#time {
  float: left;
  font-family: 'NanumSquare';
  font-size: 16px;
  color: #666666;
  margin-top: 8px;
  margin-left: 5px;
}

#time-icon {
  clear: left;
  width: 13px;
  float: left;
  margin-left: 23px;
  margin-top: 10px;
}

#pin-icon {
  width: 13px;
  float: left;
  margin-left: 23px;
  margin-top: 11px;
}

</style>


