<template>
	    <transition name="modal">
        <div v-if="!first">
          <div class="modal-mask">
              <div class="modal-wrapper">
                  <div class="modal-container">

                      <div class="modal-header">
                          <h3>방 만들기</h3>
                          <p id="addition">나에게 꼭 맞는 장보고러를 찾기 위해 태그를 선택해주세요.</p>
                      </div>

                      <div class="modal-body">
                          <p class="subtitle">#나는 이런 장보고러</p>
                          <GreenTagList :items="myAttribute" v-if="myAttribute.length > 0" />
                          <p class="subtitle" id="second">#주로 사는 것</p>
                          <GreenTagList :items="usualItem" v-if="usualItem.length > 0" />
                      </div>

  <!--                     <div class="modal-footer">
                          <button class="modal-default-button" @click="$emit('close'); addTopic()">Make</button>
                      </div> -->
                      <button id="made-room" @click="$emit('close'); addRoom()">방 만들기</button>
                  </div>
              </div>
          </div>
        </div>

        <div v-if="first">
          <div class="modal-mask">
              <div class="modal-wrapper">
                  <div class="modal-container">

                      <div class="modal-header">
                          <h3>방 만들기</h3>
                      </div>

                      <div class="modal-body">
                          <p class="title">방 제목</p>
                          <input id="room-name" type="text" placeholder="방 제목을 입력해주세요." v-model="title"/>
                          <p id="detail-info" v-model="description">최대 30자까지 입력 가능합니다.</p><br/>
                          <p class="title" id="text-box">상세설명</p>
                          <textarea id="detail" placeholder="상세설명 300자 입력"></textarea>
                          <p class="title">인원 선택</p>

                          <div class="btn-group">
                            <button class="member-num">둘이 장보기</button>
                            <button class="member-num">셋이 장보기</button>
                          </div>
                          
                          <table>
                            <tr id="row1">
                              <td><span class="title col1">주소</span></td>
                              <td><span class="title col2">장 볼 시간</span></td>
                            </tr>
                            <tr id="row2">

                              <td><input id="loca" class="col1" type="text" placeholder="경기도 남양주시 오남읍" v-model="address"/></td>
                              <td><input id="time" class="col2" type="text" placeholder="ex)8월 1일 저녁" /></td>
                            </tr>
                          </table>
                      </div>

                      <button id="made-room" @click="clickToNext()">다음</button>
                  </div>
              </div>
          </div>
        </div>
    </transition>
</template>

<script type="text/javascript">
import eventBus from './EventBus.vue'
import GreenTagList from './GreenTagList'

export default {
  name: 'new-room',
  components: { GreenTagList },
  created: function() {
    eventBus.$on('add-tag', this.addTag)
    this.uid = this.$store.state.email
  },
  methods: {
    addRoom: function() {
      // eventBus.$emit('add-room')
      console.log('Add room');
      var vm = this
      this.$axios.post('http://52.78.159.170/' + 'test1234' + '/room/',
        {
          title: this.title,
          description: this.description,
          address: this.address,
          shoppingType: this.shoppingType,
          isDisable: this.isDisable
        },
        {headers: { 'Content-Type': 'application/json' }}
        )
        .then((response) => {
          console.log(response)
          vm.result = response.data

        })
        .catch((ex) => {
          console.log('ERROR: ' + ex)
        })
    },
    clickToNext: function() {
      this.first = false
    },
    addTag: function(tag) {
      this.shoppingType.push(tag)
    }
  },
  data(){
    return {
      myAttribute: ['돔','성별','고등어','김치찌개','가쓰오부시','고등어','돼지김치찌개','돔','성별'],
      usualItem: ['돔','성별','고등어','김치찌개','가쓰오부시','고등어','돼지김치찌개','돔','성별'],
      first: true,
      title: '',
      description: '',
      address: '',
      shoppingType: [],
      isDisable: false,
      uid: ''
    }
  }
}

</script>


<style scoped>
@import url(https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css);

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(119,192,23, .84);
  display: table;
  transition: opacity .3s ease;
  font-family: 'NanumSquare';
}

.modal-wrapper {
  /*height: 610px;*/
  display: table-cell;
  vertical-align: middle;
  z-index: 9998;
}

.modal-container {
  width: 381px;
  height: 610px;
  border-radius: 10px;
  margin: 0px auto;
  padding: 20px 30px 30px;
  background-color: #fff;
  /*border-radius: 2px;*/
  /*box-shadow: 0 2px 8px rgba(0, 0, 0, .33);*/
  box-shadow: -4.8px 13.2px 40px 0 rgba(0, 0, 0, 0.15);
  transition: all .3s ease;
  /*font-family: Helvetica, Arial, sans-serif;*/
  font-family: 'NanumSquare';
  z-index: 9998;
}

.modal-header h3 {
  margin-top: 7px;
  color: #77c017;
  font-size: 20px;
}

#addition {
  margin-top: 14px;
  font-size: 8px;
  color: #595959;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.subtitle {
  clear: left;
  float: left;
  margin-left: 18px;
  font-size: 18px;
  color: #595959;
}

#made-room {
  /*position: absolute;*/
  width: 306px;
  height: 45px;
  background-color: #77c017;
  color: #ffffff;
  /*bottom: 0;*/
  border: 0;
  margin-top: 32px;
}

#second {
  margin-top: 40px;
}

.title {
  float: left;
  font-size: 13px;
  color: #474747;
  margin-left: 18px;
}

#room-name {
  clear: left;
  width: 306px;
  float: left;
  margin-left: 18px;
  border: 0;
  border-bottom: 1px solid #bfbfbf;
  margin-top: 6px;
}

#room-name::placeholder {
  /*float: left;*/
  color: #bdbdbd;
  font-size: 14px;
}

#detail-info {
  clear: left;
  float: left;
  margin-left: 18px;
  font-size: 11px;
  color: #bdbdbd;
}

#text-box{
  clear: left;
  margin-top: 10px;
}

#detail {
  width: 306px;
  height: 160px;
  float: left;
  margin-left: 20px;
  border: 1px solid #bdbdbd;
  margin-top: 5px;
  margin-bottom: 20px;
}

#detail::placeholder {
  color: #bdbdbd;
  font-size: 14px;
  padding-top: 10px;
  padding-left: 12px;
}

#loca {
  width: 190px;
  clear: left;
  /*width: 306px;*/
  float: left;
  margin-left: 18px;
  border: 0;
  border-bottom: 1px solid #bfbfbf;
  margin-top: 6px;
}

#loca::placeholder, #time::placeholder {
  color: #bdbdbd;
  font-size: 13px;
}


#time {
  width: 95px;
  clear: left;
  /*width: 306px;*/
  float: left;
  margin-left: 18px;
  border: 0;
  border-bottom: 1px solid #bfbfbf;
  margin-top: 6px;

}

.member-num {
  width: 154px;
  height: 35px;
  display: inline;
  color: #989898;
  font-size: 14px;
  border: 1px solid #bdbdbd;
}


.btn-group {
  clear: left;
  margin-left: 20px;
  margin-top: 9px;
  margin-bottom: 15px;
}


.btn-group button {
    /*background-color: #4CAF50; /* Green background */
    /*border: 1px solid green; /* Green border */
    color: #989898; /* White text */
    /*padding: 10px 10px; /* Some padding */
    cursor: pointer; /* Pointer/hand icon */
    float: left; /* Float the buttons side by side */
}

/* Clear floats (clearfix hack) */
.btn-group:after {
    content: "";
    clear: both;
    display: table;
}

.btn-group button:not(:last-child) {
    border-right: none; /* Prevent double borders */
}

table {
  text-align: left;
  /*border: 1px solid #000000;*/
}

#row1 {

}

#row2 {

}

.col1 {
  width: 198px;
}

.col2 {

}
</style>


