<template>
  <span id="one-tag" @click="addTag()">{{ data }}</span>
</template>

<script type="text/javascript">
import eventBus from './EventBus.vue'

export default {
  name: 'green-tag',
  props: [ 'data' ],
  methods: {
  	addTag: function() {
  		var element = document.getElementById("one-tag");
   		element.classList.add("mystyle");
   		eventBus.$emit('add-tag', this.data);
   		console.log(this.data);
  	}
  }
}
</script>

<style scoped>
@import url(https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css);

span {
	display: inline-block;
	height: 20px;
	color: #bfbfbf;
	font-family: 'NanumSquare';
	border: solid 1px #bfbfbf;
	border-radius: 23px;
	padding-right: 18px;
	padding-left: 18px;
	padding-top: 12px;
	padding-bottom: 12px;
	font-size: 16px;
	margin-right: 10px;
	margin-left: 0px;
	margin-top: 10px;
}

.mystyle {
	border: 0px;
	color: #ffffff;
	background-color: #77c017;
}
</style>


