<template>
	    <transition name="modal">
        <div v-if="!first">
          <div class="modal-mask">
              <div class="modal-wrapper">
                  <div class="modal-container">

                      <div class="modal-header">
                          <h3>방 만들기</h3>
                          <p id="addition">나에게 꼭 맞는 장보고러를 찾기 위해 태그를 선택해주세요.</p>
                      </div>

                      <div class="modal-body">
                          <p class="subtitle">#나는 이런 장보고러</p>
                          <GreenTagList :items="myAttribute" v-if="myAttribute.length > 0" />
                          <p class="subtitle" id="second">#주로 사는 것</p>
                          <GreenTagList :items="usualItem" v-if="usualItem.length > 0" />
                      </div>

  <!--                     <div class="modal-footer">
                          <button class="modal-default-button" @click="$emit('close'); addTopic()">Make</button>
                      </div> -->
                      <button id="made-room" @click="$emit('close');">방 만들기</button>
                  </div>
              </div>
          </div>
        </div>

        <div v-if="first">
          <div class="modal-mask">
              <div class="modal-wrapper">
                  <div class="modal-container">

                      <div class="modal-header">
                          <h3>방 만들기</h3>
                      </div>

                      <div class="modal-body">
                          <p class="title">방 제목</p><br/>
                          <input id="room-name" type="text" placeholder="방 제목을 입력해주세요." />
                          <p id="detail-info">최대 30자까지 입력 가능합니다.</p> <br/>
                          <p class="title">상세설명</p><br/>
                          <textarea id="detail" placeholder="상세설명 300자 입력"></textarea>
                          <p class="title">인원 선택</p><br/>
                          <button class="member-num">둘이 장보기</button>
                          <button class="member-num">셋이 장보기</button>
                          <p class="title">주소</p><br/>
                          <input id="loca" type="text" placeholder="경기도 남양주시 오남읍 현대아파트" />
                          <p class="title">장 볼 시간</p><br/>
                          <input id="time" type="text" placeholder="ex)8월 1일 저녁" />
                      </div>

  <!--                     <div class="modal-footer">
                          <button class="modal-default-button" @click="$emit('close'); addTopic()">Make</button>
                      </div> -->
                      <button id="made-room" @click="clickToNext()">다음</button>
                  </div>
              </div>
          </div>
        </div>
    </transition>
</template>

<script type="text/javascript">
import eventBus from './EventBus.vue'
import GreenTagList from './GreenTagList'

export default {
  name: 'new-room',
  components: { GreenTagList },
  methods: {
    addRoom: function() {
      eventBus.$emit('add-room')
    },
    clickToNext: function() {
      this.first = false
    }
  },
  data(){
    return {
      myAttribute: ['돔','성별','고등어','김치찌개','가쓰오부시','고등어','돼지김치찌개','돔','성별'],
      usualItem: ['돔','성별','고등어','김치찌개','가쓰오부시','고등어','돼지김치찌개','돔','성별'],
      first: true
    }
  }
}

</script>


<style scoped>
@import url(https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css);

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(119,192,23, .84);
  display: table;
  transition: opacity .3s ease;
  font-family: 'NanumSquare';
}

.modal-wrapper {
  /*height: 610px;*/
  display: table-cell;
  vertical-align: middle;
  z-index: 9998;
}

.modal-container {
  width: 381px;
  height: 610px;
  border-radius: 10px;
  margin: 0px auto;
  padding: 20px 30px 30px;
  background-color: #fff;
  /*border-radius: 2px;*/
  /*box-shadow: 0 2px 8px rgba(0, 0, 0, .33);*/
  box-shadow: -4.8px 13.2px 40px 0 rgba(0, 0, 0, 0.15);
  transition: all .3s ease;
  /*font-family: Helvetica, Arial, sans-serif;*/
  font-family: 'NanumSquare';
  z-index: 9998;
}

.modal-header h3 {
  margin-top: 7px;
  color: #77c017;
  font-size: 20px;
}

#addition {
  margin-top: 14px;
  font-size: 8px;
  color: #595959;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.subtitle {
  float: left;
  margin-left: 18px;
  font-size: 18px;
  color: #595959;
}

#made-room {
  /*position: absolute;*/
  width: 306px;
  height: 45px;
  background-color: #77c017;
  color: #ffffff;
  /*bottom: 0;*/
  border: 0;
  margin-top: 32px;
}

#second {
  margin-top: 40px;
}

.title {
  float: left;
  font-size: 13px;
  color: #474747;
  margin-left: 18px;
}

#room-name {
  width: 306px;
  float: left;
  margin-left: 18px;
  border: 0;
  border-bottom: 1px solid #bfbfbf;
  margin-top: 6px;
}

#room-name::placeholder {
  color: #bdbdbd;
  font-size: 14px;
}

#detail-info {
  float: left;
  margin-left: 18px;
  font-size: 11px;
  color: #bdbdbd;
}

#detail {
  width: 306px;
  height: 187px;
}

#loca {
  width: 195px;
}


#time {
  width: 95px;

}
</style>


